<template>
  <div>
    <header class="per_center ">
      <div class="pre_title">
        <router-link class="icon-ArtboardCopy" to="/index"></router-link>
      </div>
      <h3 class="mt3">登录</h3>
    </header>
    <div class="login_body">
      <!--用户名-->
      <inputText v-model="req_login.loginName" class="iptStyle"
                 title="用户名" >
      </inputText>
      <!--密码-->
      <inputText v-model="req_login.password" class="iptStyle"
                 title="密码" type="password" >
      </inputText>
      <!--验证码-->
      <div class="yz-code" v-if="isShowCodeNum>=3">
        <inputText v-model="req_login.verifyCode" class="iptStyle"
                   title="验证码"  :showCancel="false">
        </inputText>
        <div class="detBtn YCode" :class="iScode?'':'nochose'" @click="getCode">
          <img v-if="iScode" alt="" class="verCode-img" :src="imgdata">
          <p v-if="!iScode">获取验证码</p>
        </div>
      </div>
    </div>
    <p v-if="islogin" class="login_box_p mt_35 op4 mt7">
      <a href="javaScript:void(0)" class="login_box_p_a01">确定</a>
    </p>
    <p v-else class="login_box_p mt_35 mt7">
      <a href="javaScript:void(0)" class="login_box_p_a01" @click="login">确定</a>
    </p>

  </div>
</template>

<style>
  .iptStyle:last-child {
    margin-bottom: 0.2rem !important;
  }
</style>
<style scoped>

</style>
<script>
  import {getnotice} from '../common'
  export default {
    data () {
      return {
        codeText: '获取验证码',
        iScode: false,
        isShowCodeNum: 0,
        islogin: true,
        req_login: {
          'loginName': '',
          'password': '',
          'verifyCode': '',
        },
        imgdata: ''
      }
    },
    filters: {

    },

    created: function () {

    },
    mounted: function () {

    },

    methods: {
      getCode(){
        var _t = this;
        this.$ajax.post('/common/imageCode.shtml').then(function (res) {
          if (res.status == 200 && res.data) {
            _t.imgdata = '';
            _t.imgdata = _t.$ajax.defaults.baseURL + '/common/imageCode.shtml';
            _t.iScode = false;
            setTimeout(function(){
              _t.iScode = true;
            },20)
            _t.islogin = true
          }
        }).catch(function (err) {

        });
      },

      login(){
        var _t = this;
        var data = this.req_login;
        this.$ajax.post('common/b2b/login.shtml', data).then(function (res) {
          if (res.data.result == 1) {
            getnotice('登录成功');
            setTimeout(function () {
              _t.$router.push('/index');
            }, 800)
          } else if (res.data.result === 0) {
            _t.isShowCodeNum++
          }
        }).catch(function (err) {

        });
      },
    },
    watch: {
      req_login: {
        handler(val, oldVal){
          if (this.isShowCodeNum >= 3) {
            (val.loginName && val.password && val.verifyCode.length == 4) ? this.islogin = false : this.islogin = true
          } else {
            (val.loginName && val.password) ? this.islogin = false : this.islogin = true
          }
        },
        deep: true
      },
      isShowCodeNum(val){
        var _t = this;
        if (val == 3) {
          setTimeout(function () {
            _t.getCode()
          }, 10)
        }
      }
    },
  }
</script>



